.modal {
    display: flex;
    flex-direction: column;
    border-radius: 1em;
    padding: 0.5em;
    max-height: 35em;
    font-weight: 600;
    box-shadow: 3px 3px 2px 2px rgba(0, 0, 0, 0.25);
    width: 300px;
    margin-top: 2em;
    background-color: white;

}

/* keep this */
.modal-image {
    width: 18em;

}


.cohort-1 {
    display: flex;
}

.cohort-paragraphs {
    padding-right: 2em;
    font-weight: 600;
    color: black;
}

a {
    color: black;
}

.cohort-date {}

.cohort-1 button {
    background: #9B0E5B;
    color: white;
    width: 120px;
    height: 40px;
    border: 2px solid black;
    border-radius: 6px;
    font-weight: 700;

}


@media only screen and (max-width: 768px) {
    .modal {
        width: 20em;
        align-items: center;

    }

    .pricing-content {
        width: 100%;
        align-items: center;
    }

    .pricing-content ul {
        width: 100%;
    }
}